import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import "./css/Popup.css";

function Popup({ isOpen, onClose }) {
  const [dontShowToday, setDontShowToday] = useState(false);
  const [visible, setVisible] = useState(false); //   

  //   
  useEffect(() => {
    const saved = localStorage.getItem("popupClosedUntil");
    if (saved && new Date(saved) > new Date()) {
      setVisible(false); //     
    } else {
      setVisible(isOpen); //  isOpen
    }
  }, [isOpen]);

  const handleClose = () => {
    if (dontShowToday) {
      const expireTime = new Date();
      expireTime.setHours(expireTime.getHours() + 24);
      localStorage.setItem("popupClosedUntil", expireTime.toISOString());
    }
    setVisible(false);
    onClose();
  };

  if (!visible) return null;

  return (
    <div className="popup-overlay">
      <div className="puclose">
        <input
          type="checkbox"
          id="pudayclose1"
          checked={dontShowToday}
          onChange={(e) => setDontShowToday(e.target.checked)}
        />
        <label htmlFor="pudayclose1">   </label>
        <button onClick={handleClose} className="puclosebtn">
          
        </button>
      </div>
      <div className="pucontainer">
        <div className="pucontents">
          <img
            src="https://yuriyuri01.github.io/pina_img/pinaimg/dt_etc3.png"
            alt="dt_etc3"
          />
          <span className="puspan1">
            *&nbsp;&nbsp;    &nbsp;&nbsp;*
          </span>
          <span className="puspan2">
                   !
          </span>
        </div>
        <Link to={"/Lottery"}>
          <button className="pulotbtn"> </button>
        </Link>
      </div>
    </div>
  );
}

export default Popup;
