import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "./css/Orderlist.css";

const formatKRW = (n) => n.toLocaleString("ko-KR");

export default function OrderList() {
  const [orders, setOrders] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    const latestOrder = localStorage.getItem("latestOrder");
    if (latestOrder) {
      setOrders([JSON.parse(latestOrder)]);
    }
  }, []);

  const goBrowse = () => navigate("/CategoryDetail");

  return (
    <div id="s1OL">
      <div id="s1boxOL">
        <h1 className="h1OL"> </h1>

        {/*    */}
        {(!orders || orders.length === 0) && (
          <div id="s1noneOL">
            <div id="noneboxOL">
              <div className="nonecontentOL">
                <span className="nonespanOL"> .</span>
                <div className="noneitembtnOL" role="button" onClick={goBrowse}>
                   
                </div>
              </div>
            </div>
          </div>
        )}

        {/*    */}
        {orders &&
          orders.length > 0 &&
          orders.map((order) => {
            const total = order.items.reduce(
              (acc, it) =>
                acc +
                (it.totalPrice ??
                  it.price * (it.mainCount || 1) +
                    it.optionPrice * (it.optionCount || 0)),
              0
            );

            return (
              <div className="s1content1OL" key={order.id}>
                <div className="conbox1OL">
                  <div>
                    
                    <br />
                    {order.id}
                  </div>
                  <br />
                  <div>
                    
                    <br />
                    {order.date}
                  </div>
                </div>

                <div className="conbox2OL">
                  {order.items.map((it, i) => (
                    <div key={i} className="itemboxOL">
                      <div className="itemimgOL">
                        <img src={it.img} alt={it.productName || it.name} />
                        <div
                          className="rvbtnOL"
                          role="button"
                          onClick={() => {
                            if (it.productName.includes("")) {
                              navigate("/detail", {
                                state: { openReview: true },
                              });
                            } else if (it.productName.includes("")) {
                              navigate("/detail2", {
                                state: { openReview: true },
                              });
                            }
                          }}
                        >
                           
                        </div>
                      </div>
                      <div className="itemnameOL">
                        {it.productName || it.name}
                      </div>
                      <div className="itempriceOL">
                        {formatKRW(
                          it.totalPrice - it.optionCount * it.optionPrice
                        )}
                        
                        <span>
                          (  :{" "}
                          {formatKRW(it.optionPrice * it.optionCount)} )
                        </span>
                      </div>
                      <div className="itemdlvOL">
                        {it.deliveryType === ""
                          ? ""
                          : ""}
                      </div>
                      <div className="itemtotalOL">
                        :{" "}
                        {formatKRW(
                          it.totalPrice ??
                            it.price * (it.mainCount || 1) +
                              it.optionPrice * (it.optionCount || 0)
                        )}
                        
                      </div>
                    </div>
                  ))}
                </div>

                <div className="conbox3OL">
                    : {formatKRW(order.finalTotal)}
                  {order.discountAmount > 0 && (
                    <span
                      style={{
                        color: "#2E5245",
                        marginLeft: "10px",
                        fontSize: "15px",
                        fontWeight: "500",
                      }}
                    >
                      (   : - {formatKRW(order.discountAmount)} )
                    </span>
                  )}
                  {order.discountAmount < 0 && (
                    <span
                      style={{
                        color: "#2E5245",
                        marginLeft: "10px",
                        fontSize: "15px",
                        fontWeight: "500",
                      }}
                    >
                      (   : {formatKRW(-order.discountAmount)} )
                    </span>
                  )}
                </div>
              </div>
            );
          })}
      </div>
    </div>
  );
}
