import "./css/PaymentCompleted.css";
import { useNavigate, Link, useLocation } from "react-router-dom";
import { useEffect, useState, useContext } from "react";
import { CartContext } from "../CartContext"; // CartContext import

export default function PaymentCompleted({ loggedInUser }) {
  const navigate = useNavigate();
  const location = useLocation();
  const { removeItems } = useContext(CartContext); // removeItems 

  const paidItems = location.state?.paidItems || [
    {
      id: 1,
      name: " ",
      price: 12000,
      status: " ",
      img: "https://yuriyuri01.github.io/pina_img/pinaimg/cartimg1.png",
      mainCount: 1,
      optionPrice: 0,
      optionCount: 0,
      totalPrice: 12000,
      deliveryType: "normal",
      optionLabel: "",
    },
  ];

  const removeKeys = location.state?.removeKeys || []; // MessageCard   

  useEffect(() => {
    //     
    if (removeKeys.length > 0) {
      removeItems(removeKeys);
    }

    //   localStorage 
    const discountAmount = location.state?.discountAmount || 0;
    const finalTotal =
      location.state?.finalTotal ||
      paidItems.reduce((acc, it) => acc + it.totalPrice, 0);

    //   
    const now = new Date();
    const yyyy = now.getFullYear();
    const mm = String(now.getMonth() + 1).padStart(2, "0");
    const dd = String(now.getDate()).padStart(2, "0");
    const today = `${yyyy}.${mm}.${dd}`;

    localStorage.setItem(
      "latestOrder",
      JSON.stringify({
        id: "2025-000001",
        items: paidItems,
        discountAmount,
        finalTotal,
        date: today, //     
      })
    );
  }, [removeItems, removeKeys, paidItems, location.state]);

  const [showEventModal, setShowEventModal] = useState(false);
  const [confetti, setConfetti] = useState([]);
  const [event, setEvent] = useState(null);

  const goCategoryDetail = () => navigate("/CategoryDetail");

  useEffect(() => {
    //    
    const timer = setTimeout(() => {
      if (Math.random() < 0.8) {
        const eventOptions = [
          {
            img: "https://yuriyuri01.github.io/pina_img/pinaimg/pce_flower.png",
            title: " !",
            text: "    !",
          },
          {
            img: "https://yuriyuri01.github.io/pina_img/pinaimg/pce_flower2.png",
            title: " !",
            text: "   !",
          },
          {
            img: "https://yuriyuri01.github.io/pina_img/pinaimg/pce_flower3.png",
            title: " !",
            text: "   !",
          },
        ];
        const randomEvent =
          eventOptions[Math.floor(Math.random() * eventOptions.length)];
        if (loggedInUser?.name) {
          randomEvent.title = `${loggedInUser.name}, ${randomEvent.title}`;
        }
        setEvent(randomEvent);
        setShowEventModal(true);
      }
    }, 100);

    return () => clearTimeout(timer);
  }, [loggedInUser]);

  // 
  useEffect(() => {
    if (showEventModal) {
      const confettiArray = [];
      for (let i = 0; i < 100; i++) {
        confettiArray.push({
          id: i,
          left: Math.random() * 100 + "vw",
          size: 5 + Math.random() * 10 + "px",
          bg: `hsl(${Math.random() * 360}, 100%, 50%)`,
          delay: Math.random() * 2 + "s",
          duration: 3 + Math.random() * 3 + "s",
          rotate: Math.random() * 360 + "deg",
        });
      }
      setConfetti(confettiArray);
    } else {
      setConfetti([]);
    }
  }, [showEventModal]);

  const closeModal = () => setShowEventModal(false);

  const [today, setToday] = useState("");

  useEffect(() => {
    const now = new Date();
    const yyyy = now.getFullYear();
    const mm = String(now.getMonth() + 1).padStart(2, "0"); //  0 
    const dd = String(now.getDate()).padStart(2, "0");
    setToday(`${yyyy}.${mm}.${dd}`);
  }, []);

  return (
    <>
      <div id="sec1allPC">
        <h1 className="sec1titlePC"> </h1>
        <div className="sec1contentPC">
          <div className="contenttopPC">
            <img
              src={
                paidItems[0]?.img ||
                "https://yuriyuri01.github.io/pina_img/pinaimg/pc_flower.png"
              }
              alt={paidItems[0]?.productName || " "}
            />
          </div>
          <div className="contentbottomPC">
            <div className="completePC"> !</div>
            <div className="completenumPC">
              <br /> : 2025-000001
            </div>
            <div className="cpdatePC">{today}</div>
            {paidItems.length > 0 && (
              <div className="paiditemnamePC">
                <ul>
                  {paidItems.map((item, idx) => (
                    <li key={idx}>{item.productName || item.name}</li>
                  ))}
                </ul>
              </div>
            )}
          </div>
        </div>
        <div id="sec1bottomPC">
          <Link to={"/Orderlist"}>
            <button className="sec1buttonleftPC"> </button>
          </Link>
          <button className="sec1buttonrightPC" onClick={goCategoryDetail}>
             
          </button>
        </div>
      </div>

      {confetti.map((c) => (
        <div
          key={c.id}
          className="confetti"
          style={{
            left: c.left,
            width: c.size,
            height: c.size,
            backgroundColor: c.bg,
            animationDelay: c.delay,
            animationDuration: c.duration,
            transform: `rotate(${c.rotate})`,
          }}
        />
      ))}

      <div
        id="paymenteventPCE"
        className={`modalOverlay ${showEventModal ? "show" : ""}`}
        onClick={closeModal}
      >
        <div
          className="modalContentPCE prize"
          onClick={(e) => e.stopPropagation()}
        >
          {event && (
            <>
              <img src={event.img} alt="event_image" />
              <h2 className="h2PCE sparkle">{event.title}</h2>
              <p className="ptextPCE sparkle">{event.text}</p>
            </>
          )}
          <button onClick={closeModal}></button>
        </div>
      </div>
    </>
  );
}
