import { useContext, useState, useEffect } from "react";
import "./css/Mypage.css";
import { Link } from "react-router-dom";
import { AuthContext } from "../context/SocialLog_data";

const MyPage = () => {
  const ORDERS_ID = "panel-orders";
  const COUPONS_ID = "panel-coupons";

  //    
  const [activePanel, setActivePanel] = useState(ORDERS_ID);

  // URL     
  useEffect(() => {
    const hashId = window.location.hash.replace("#", "");
    if (hashId === ORDERS_ID || hashId === COUPONS_ID) {
      setActivePanel(hashId);
    }
  }, []);

  //   /   
  const handleMenuClick = (panelTarget) => {
    if (panelTarget) {
      //      
      setActivePanel(panelTarget);
    } else {
      //      / 
      if (activePanel === COUPONS_ID) setActivePanel(ORDERS_ID);
    }
  };

  //  
  const [localUser, setLocalUser] = useState(null);
  //  
  const { user: socialUser } = useContext(AuthContext);
  //   
  const [coupons, setCoupons] = useState([]);

  useEffect(() => {
    const storedUser = localStorage.getItem("loggedInUser");
    if (storedUser) {
      setLocalUser(JSON.parse(storedUser));
    }
  }, []);

  //    ( > )
  const currentUser = socialUser || localUser;
  //    
  useEffect(() => {
    if (currentUser?.id) {
      const storedCoupons =
        JSON.parse(localStorage.getItem(`coupons_${currentUser.id}`)) || [];
      setCoupons(storedCoupons);
    }
  }, [currentUser]);

  const { logout: socialLogout } = useContext(AuthContext);

  const handleLogout = () => {
    localStorage.removeItem("loggedInUser"); // 
    socialLogout(); // 
    alert(" .");
    window.location.href = "/pina0";
  };

  return (
    <div id="mypage">
      {/*   */}
      <div className="my_title">
        <h1></h1>
      </div>

      {/*  &   */}
      <div className="my_introduce">
        <div className="my_name">
          <p className="my_hello">
            , {currentUser ? currentUser.name : ""}
          </p>
          <div className="my_alarm">
              5% 
            <br />
               .
          </div>
        </div>

        <div className="my_current">
          <div className="current_card">
            <div className="my_rank">
              <p className="title"></p>
              <div className="my_value">
                <p className="main">VIP</p>
                <a href="#none" className="sub">
                  
                  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48">
                    <path
                      fill="none"
                      stroke="currentColor"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      stroke-width="4"
                      d="m19 12l12 12l-12 12"
                    />
                  </svg>
                </a>
              </div>
            </div>

            <div className="my_line"></div>

            <div className="my_coupon">
              <p className="title"></p>
              <div className="my_value">
                <p className="main">{coupons.length}</p>
                <span
                  className="sub toggle-panel"
                  onClick={() => handleMenuClick(COUPONS_ID)}
                  style={{ cursor: "pointer" }}
                >
                  
                  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48">
                    <path
                      fill="none"
                      stroke="currentColor"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      stroke-width="4"
                      d="m19 12l12 12l-12 12"
                    />
                  </svg>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/*   +   */}
      <div className="my_accessBox">
        {/*   */}
        <div className="my_search">
          <div className="my_hmmm">
            <div className="my_order" onClick={() => handleMenuClick()}>
              <p>/</p>
              <p>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48">
                  <path
                    fill="none"
                    stroke="currentColor"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="4"
                    d="m19 12l12 12l-12 12"
                  />
                </svg>
              </p>
            </div>
            <Link to={"/Orderlist"}>
              <div className="my_order" onClick={() => handleMenuClick()}>
                <p> </p>
                <p>
                  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48">
                    <path
                      fill="none"
                      stroke="currentColor"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      stroke-width="4"
                      d="m19 12l12 12l-12 12"
                    />
                  </svg>
                </p>
              </div>
            </Link>
            <div className="my_order" onClick={() => handleMenuClick()}>
              <p> </p>
              <p>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48">
                  <path
                    fill="none"
                    stroke="currentColor"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="4"
                    d="m19 12l12 12l-12 12"
                  />
                </svg>
              </p>
            </div>
          </div>
          <div className="my_hmmm02">
            <div className="my_order" onClick={() => handleMenuClick()}>
              <p> </p>
              <p>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48">
                  <path
                    fill="none"
                    stroke="currentColor"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="4"
                    d="m19 12l12 12l-12 12"
                  />
                </svg>
              </p>
            </div>
            <div className="my_order" onClick={() => handleMenuClick()}>
              <p> </p>
              <p>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48">
                  <path
                    fill="none"
                    stroke="currentColor"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="4"
                    d="m19 12l12 12l-12 12"
                  />
                </svg>
              </p>
            </div>
            <div className="my_order" onClick={() => handleMenuClick()}>
              <p> </p>
              <p>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48">
                  <path
                    fill="none"
                    stroke="currentColor"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="4"
                    d="m19 12l12 12l-12 12"
                  />
                </svg>
              </p>
            </div>

            <div
              className="my_order"
              onClick={() => handleMenuClick(COUPONS_ID)}
            >
              <p></p>
              <p>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48">
                  <path
                    fill="none"
                    stroke="currentColor"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="4"
                    d="m19 12l12 12l-12 12"
                  />
                </svg>
              </p>
            </div>

            <Link
              className="my_order"
              to={"/qna"}
              onClick={() => handleMenuClick()}
            >
              <p className="qna-link">1:1 </p>
              <p>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48">
                  <path
                    fill="none"
                    stroke="currentColor"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="4"
                    d="m19 12l12 12l-12 12"
                  />
                </svg>
              </p>
            </Link>
          </div>

          <button className="my_logout" onClick={handleLogout}>
            
          </button>
        </div>

        {/*   */}
        <div className="my_access">
          {/* /  */}
          <div
            className="panel"
            id={ORDERS_ID}
            hidden={activePanel !== ORDERS_ID}
          >
            <h1>/ </h1>
            <div className="my_delivery">
              <div className="my_deliWait">
                <p>0</p>
                <p> </p>
              </div>
              <div>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48">
                  <path
                    fill="none"
                    stroke="currentColor"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="4"
                    d="m19 12l12 12l-12 12"
                  />
                </svg>
              </div>
              <div className="my_deliWait">
                <p>0</p>
                <p> </p>
              </div>
              <div>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48">
                  <path
                    fill="none"
                    stroke="currentColor"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="4"
                    d="m19 12l12 12l-12 12"
                  />
                </svg>
              </div>
              <div className="my_deliWait">
                <p>0</p>
                <p></p>
              </div>
              <div>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48">
                  <path
                    fill="none"
                    stroke="currentColor"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="4"
                    d="m19 12l12 12l-12 12"
                  />
                </svg>
              </div>
              <div className="my_deliWait">
                <p>0</p>
                <p> </p>
              </div>
            </div>

            <div className="my_history">
              <p> </p>
              <div>
                <p>   .</p>
              </div>
            </div>

            <div className="my_service">
              <div className="service_box">
                <p> </p>
                <p>   </p>
              </div>
              <div className="service_box">
                <p> </p>
                <p> </p>
              </div>
            </div>
          </div>

          {/*   */}
          <div
            className="panel"
            id={COUPONS_ID}
            hidden={activePanel !== COUPONS_ID}
          >
            <h1> </h1>
            <div className="coupon_box">
              <form
                className="coupon_form"
                onSubmit={(e) => e.preventDefault()}
              >
                <input
                  className="coupon_input"
                  type="text"
                  placeholder="12 . ex)0000-0000-0000"
                />
                <button className="coupon_btn" type="submit">
                   
                </button>
              </form>
              {coupons.length === 0 ? (
                <div className="coupon_empty">
                      .
                </div>
              ) : (
                <ul className="coupon_list">
                  {coupons.map((coupon, idx) => {
                    //    (coupon.issuedAt    )
                    const issuedDate = coupon.issuedAt
                      ? new Date(coupon.issuedAt)
                      : new Date();
                    const expireDate = new Date(issuedDate);
                    expireDate.setDate(issuedDate.getDate() + 7);

                    //  YYYY.MM.DD  
                    const formatDate = (date) =>
                      `${date.getFullYear()}.${String(
                        date.getMonth() + 1
                      ).padStart(2, "0")}.${String(date.getDate()).padStart(
                        2,
                        "0"
                      )}`;

                    const issuedStr = formatDate(issuedDate);
                    const expireStr = formatDate(expireDate);
                    return (
                      <li key={idx}>
                        {" "}
                        <span>{coupon.text}</span>
                        <span className="coupon_expire">
                          {issuedStr} ~ {expireStr}
                        </span>
                      </li>
                    );
                  })}
                </ul>
              )}
              <ul
                className="coupon_pagination"
                role="navigation"
                aria-label=" "
              >
                <li className="page prev">
                  <a href="#none" aria-label=" ">
                    <span class="iconamoon--arrow-right-2 left" />
                  </a>
                </li>
                <li className="page is-current">
                  <a href="#none" aria-current="page">
                    1
                  </a>
                </li>
                <li className="page next">
                  <a href="#none" aria-label=" ">
                    <span class="iconamoon--arrow-right-2" />
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MyPage;
