import React, { useState, useEffect, useContext } from "react";
import "./css/Login.css";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../context/SocialLog_data";

import SocialLog from "./SocialLog"; //  SocialLog import 

export default function Login({ isActive, closeLogin }) {
  const navigate = useNavigate();
  const [id, setId] = useState("");
  const [password, setPassword] = useState("");
  const [loggedInUser, setLoggedInUser] = useState(null);

  //    localStorage 
  useEffect(() => {
    const storedUser = localStorage.getItem("loggedInUser");
    if (storedUser) {
      setLoggedInUser(JSON.parse(storedUser));
    }
  }, []);

  const goMember = () => {
    closeLogin();
    navigate("/member");
  };
  const handleLogin = (e) => {
    e.preventDefault();
    const users = JSON.parse(localStorage.getItem("users") || "[]");
    const user = users.find((u) => u.id === id && u.password === password);

    if (user) {
      alert(`, ${user.name}!`);
      localStorage.setItem("loggedInUser", JSON.stringify(user));
      setLoggedInUser(user); //  

      window.dispatchEvent(new Event("userChange"));
    } else {
      alert("    .");
    }
  };

  const goMyPage = () => {
    closeLogin();
    navigate("/mypage");
  };

  const { logout: socialLogout } = useContext(AuthContext);

  const handleLogout01 = () => {
    localStorage.removeItem("loggedInUser"); // 
    setLoggedInUser(null);
    window.dispatchEvent(new Event("userChange"));
    socialLogout(); // 
    alert(" .");
    navigate("/");
  };

  return (
    <div id="loginBox" className={isActive ? "active" : ""}>
      <div id="bgShadow" onClick={closeLogin}></div>

      <div id="loginMain">
        <svg className="lgclosebtn" onClick={closeLogin}>
          <path
            fill="none"
            stroke="currentColor"
            strokeDasharray="12"
            strokeDashoffset="12"
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth="2"
            d="M12 12l7 7M12 12l-7 -7M12 12l-7 7M12 12l7 -7"
          >
            <animate
              fill="freeze"
              attributeName="stroke-dashoffset"
              dur="0.3s"
              values="12;0"
            />
          </path>
        </svg>

        <span className="lgtitlespan"></span>
        <span className="lgspan1">
               .
        </span>

        {loggedInUser ? (
          //    
          <div className="after-login">
            {/*    */}
            <div className="welcome-box">
              <p>{loggedInUser.name}, .</p>
            </div>

            {/*   */}
            <div className="button-box">
              <button className="lgmembtn" onClick={goMyPage}>
                
              </button>
              <button className="lgloginbtn" onClick={handleLogout01}>
                
              </button>
            </div>
          </div>
        ) : (
          //    
          <form className="lgform" onSubmit={handleLogin}>
            <input
              type="text"
              className="lgidbox"
              placeholder="  *"
              value={id}
              onChange={(e) => setId(e.target.value)}
            />
            <input
              type="password"
              className="lgpwbox"
              placeholder="  *"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
            <span className="lgpwspan"> ?</span>
            <button className="lgloginbtn" type="submit">
              
            </button>
            <button className="lgmembtn" type="button" onClick={goMember}>
              
            </button>
            <SocialLog setLoggedInUser={setLoggedInUser} />
            <div className="lgline"></div>
          </form>
        )}
      </div>
    </div>
  );
}
