import { useState } from "react";
import { useNavigate } from "react-router-dom";
import "./css/Member.css";

const Member = () => {
  const navigate = useNavigate();
  const [id, setId] = useState("");
  const [idAvailable, setIdAvailable] = useState(null);
  const [loading, setLoading] = useState(false);

  const [agreements, setAgreements] = useState({
    all: false,
    terms: false,
    privacy: false,
    outsourcing: false,
    overseas: false,
    marketingEmail: false,
    marketingSMS: false,
  });

  const [password, setPassword] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");

  //     
  const handleIdCheck = () => {
    if (!id.trim()) {
      alert(" .");
      return;
    }
    setLoading(true);
    setTimeout(() => {
      const users = JSON.parse(localStorage.getItem("users") || "[]");
      const exists = users.some((user) => user.id === id);
      setIdAvailable(!exists);
      setLoading(false);
    }, 500); //  
  };

  const handleCheckboxChange = (e) => {
    const { name, checked } = e.target;
    setAgreements((prev) => {
      const updated = { ...prev, [name]: checked };
      const { all, ...subs } = updated;
      updated.all = Object.values(subs).every((v) => v === true);
      return updated;
    });
  };

  const handleAllCheck = (e) => {
    const checked = e.target.checked;
    setAgreements({
      all: checked,
      terms: checked,
      privacy: checked,
      outsourcing: checked,
      overseas: checked,
      marketingEmail: checked,
      marketingSMS: checked,
    });
  };

  //  
  const handleSubmit = (e) => {
    e.preventDefault();

    if (!idAvailable) {
      alert("   .");
      return;
    }

    if (!password || password !== passwordConfirm) {
      alert("  .");
      return;
    }

    if (
      !agreements.terms ||
      !agreements.privacy ||
      !agreements.outsourcing ||
      !agreements.overseas
    ) {
      alert("  .");
      return;
    }

    //  
    const users = JSON.parse(localStorage.getItem("users") || "[]");
    users.push({
      id,
      password,
      email,
      name,
      phone,
      agreements,
    });
    localStorage.setItem("users", JSON.stringify(users));

    alert(" !");
    //  
    setId("");
    setIdAvailable(null);
    setPassword("");
    setPasswordConfirm("");
    setEmail("");
    setName("");
    setPhone("");
    setAgreements({
      all: false,
      terms: false,
      privacy: false,
      outsourcing: false,
      overseas: false,
      marketingEmail: false,
      marketingSMS: false,
    });

    navigate("/");
  };

  return (
    <div id="memberMB">
      <h1 id="h1MB"></h1>
      <span id="memberspanMB">
             .
        <br />*       .
      </span>

      <form className="formMB" onSubmit={handleSubmit}>
        <div className="idboxMB">
          <input
            className="idMB"
            type="text"
            placeholder=""
            value={id}
            onChange={(e) => setId(e.target.value)}
            required
            autoComplete="new-password"
          />
          <button
            className="idcheckMB"
            type="button"
            onClick={handleIdCheck}
            disabled={loading}
          >
            {loading ? " ..." : ""}
          </button>
        </div>

        {idAvailable === true && (
          <p style={{ color: "green", fontSize: "14px" }}>
               
          </p>
        )}
        {idAvailable === false && (
          <p style={{ color: "red", fontSize: "14px" }}>
               
          </p>
        )}

        <input
          className="pwMB"
          type="password"
          placeholder=""
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          required
          autoComplete="new-password"
        />
        <input
          className="pwcMB"
          type="password"
          placeholder=" "
          value={passwordConfirm}
          onChange={(e) => setPasswordConfirm(e.target.value)}
          required
        />
        <input
          className="emailMB"
          type="email"
          placeholder=""
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
        />
        <input
          className="nameMB"
          type="text"
          placeholder=""
          value={name}
          onChange={(e) => setName(e.target.value)}
          required
        />
        <input
          className="phoneMB"
          type="tel"
          placeholder=""
          value={phone}
          onChange={(e) => setPhone(e.target.value)}
          required
        />

        <div className="chballMB">
          <input
            type="checkbox"
            name="all"
            checked={agreements.all}
            onChange={handleAllCheck}
            id="chballcb"
          />
          <label htmlFor="chballcb">  .</label>
        </div>

        <div className="chbsubMB">
          {/*     */}
          <div className="chbsub1MB">
            <input
              type="checkbox"
              name="terms"
              checked={agreements.terms}
              onChange={handleCheckboxChange}
              required
              id="chbsub1cb"
            />
            <label htmlFor="chbsub1cb">  . *</label>
          </div>
          <div className="chbsub2MB">
            <input
              type="checkbox"
              name="privacy"
              checked={agreements.privacy}
              onChange={handleCheckboxChange}
              required
              id="chbsub2cb"
            />
            <label htmlFor="chbsub2cb">
                   . *
            </label>
          </div>
          <div className="chbsub3MB">
            <input
              type="checkbox"
              name="outsourcing"
              checked={agreements.outsourcing}
              onChange={handleCheckboxChange}
              required
              id="chbsub3cb"
            />
            <label htmlFor="chbsub3cb">
                  . *
            </label>
          </div>
          <div className="chbsub4MB">
            <input
              type="checkbox"
              name="overseas"
              checked={agreements.overseas}
              onChange={handleCheckboxChange}
              required
              id="chbsub4cb"
            />
            <label htmlFor="chbsub4cb">
                  . *
            </label>
          </div>
          <div className="chbsub5MB">
            <input
              type="checkbox"
              name="marketingEmail"
              checked={agreements.marketingEmail}
              onChange={handleCheckboxChange}
              id="chbsub5cb"
            />
            <label htmlFor="chbsub5cb">
                 /   () ()
            </label>
          </div>
          <div className="chbsub6MB">
            <input
              type="checkbox"
              name="marketingSMS"
              checked={agreements.marketingSMS}
              onChange={handleCheckboxChange}
              id="chbsub6cb"
            />
            <label htmlFor="chbsub6cb">
                 /   (SMS) ()
            </label>
          </div>
        </div>

        <button className="sbmMB" type="submit">
          
        </button>
      </form>
    </div>
  );
};

export default Member;
