import { useState, useEffect, useContext } from "react";
import { CartContext } from "../CartContext";
import "./css/Payment.css";
import { useNavigate, useLocation } from "react-router-dom";

const formatKRW = (n) => n.toLocaleString("ko-KR");

export default function PaymentPage() {
  const navigate = useNavigate();
  const location = useLocation();
  const [userCoupons, setUserCoupons] = useState([]);
  const storedUser = JSON.parse(localStorage.getItem("loggedInUser"));
  const userId = storedUser?.id;
  const { removeItems } = useContext(CartContext);

  const {
    productName = " ",
    price = 59900,
    mainCount = 1,
    optionName = " ",
    optionPrice = 12000,
    optionCount = 1,
    deliveryType = "",
    totalPrice = price * mainCount + optionPrice * optionCount,
    cartItems = [], // MessageCard  
    img, //    
    selectedKeys = [],
  } = location.state || {};

  //    
  const products =
    cartItems.length > 0
      ? cartItems.map((item) => ({
          productName: item.productName || item.name,
          price: item.price,
          mainCount: item.mainCount ?? item.qty,
          optionName: item.optionName ?? item.optionLabel,
          optionPrice: item.optionPrice,
          optionCount: item.optionCount ?? item.optionQty ?? 0,
          deliveryType: item.deliveryType || "",
          totalPrice:
            item.totalPrice ??
            item.price * (item.mainCount ?? item.qty) +
              item.optionPrice * (item.optionCount ?? item.optionQty ?? 0),
          img:
            item.img || "https://kjwon2025.github.io/pinaimg/paymentimg1.png",
        }))
      : [
          {
            productName,
            price,
            mainCount,
            optionName,
            optionPrice,
            optionCount,
            deliveryType,
            totalPrice:
              (price || 59900) * (mainCount || 1) +
              (optionPrice || 12000) * (optionCount || 1),
            img: img || "https://kjwon2025.github.io/pinaimg/paymentimg1.png",
          },
        ];

  const [buyer, setBuyer] = useState({ name: "", phone: "" });
  const [sender, setSender] = useState({ name: "" });
  const [receiver, setReceiver] = useState({
    name: "",
    phone: "",
    address: "",
  });
  const [coupon, setCoupon] = useState("");
  const [payMethod, setPayMethod] = useState("card");
  const [cardInfo, setCardInfo] = useState({
    num1: "",
    num2: "",
    num3: "",
    num4: "",
    year: "",
    month: "",
    pw: "",
  });

  const handleSubmit = (e) => {
    e.preventDefault();

    const sumTotal = products.reduce(
      (acc, item) =>
        acc +
        (item.totalPrice ??
          item.price * item.mainCount + item.optionPrice * item.optionCount),
      0
    );

    //    
    if (selectedCoupon && userId) {
      const storedCoupons =
        JSON.parse(localStorage.getItem(`coupons_${userId}`)) || [];
      const updatedCoupons = storedCoupons.filter(
        (c) => c.text !== selectedCoupon.text
      );
      localStorage.setItem(`coupons_${userId}`, JSON.stringify(updatedCoupons));
    }

    //     
    if (cartItems.length > 0) {
      const keysToRemove = cartItems.map((it) => `${it.id}-${it.deliveryType}`);
      removeItems(keysToRemove); // CartContext 
    }

    //    
    navigate("/PaymentCompleted", {
      state: {
        paidItems: products,
        discountAmount,
        finalTotal,
      },
    });
  };

  useEffect(() => {
    if (userId) {
      const storedCoupons =
        JSON.parse(localStorage.getItem(`coupons_${userId}`)) || [];
      setUserCoupons(storedCoupons);
    }
  }, [userId]);

  const [selectedCoupon, setSelectedCoupon] = useState(null);

  //   
  const sumTotal = products.reduce(
    (acc, item) =>
      acc +
      (item.totalPrice ??
        item.price * item.mainCount + item.optionPrice * item.optionCount),
    0
  );

  //  
  let finalTotal = sumTotal;
  let discountAmount = 0;

  if (selectedCoupon) {
    //       
    if (selectedCoupon.text.includes("   ")) {
      finalTotal = sumTotal + 300000;
      discountAmount = -300000; //   
    } else {
      const percentMatch = selectedCoupon.text.match(/(\d+)%/);
      const wonMatch = selectedCoupon.text.match(/([\d,]+)/);

      if (percentMatch) {
        const percent = parseInt(percentMatch[1], 10);
        discountAmount = Math.floor(sumTotal * (percent / 100));
      } else if (wonMatch) {
        discountAmount = parseInt(wonMatch[1].replace(/,/g, ""), 10);
      }
      finalTotal = Math.max(0, sumTotal - discountAmount);
    }
  }

  const [couponModalVisible, setCouponModalVisible] = useState(false);

  return (
    <>
      <div id="s1PM">
        <div id="s1conPM">
          <h1 className="h1PM"> </h1>

          <div className="conboxPM">
            <h2 className="h2PM"> </h2>

            {products.map((item, idx) => (
              <div className="subcon1PM" key={idx}>
                <div className="sc1iboxPM">
                  <img
                    src={
                      item.img ||
                      "https://kjwon2025.github.io/pinaimg/paymentimg1.png"
                    }
                    alt={item.productName || item.name}
                  />
                </div>
                <div className="sc1infoPM">
                  {/*   */}
                  <div className="delihowPM">
                    {item.deliveryType === "" ? "" : ""}
                  </div>

                  <span className="infoboldPM">
                    {item.productName || item.name}
                  </span>
                  <span className="infoinfoPM">
                    {formatKRW(item.price)} /  : {item.mainCount ?? 0}
                    <br />
                    {item.optionName ?? item.optionLabel} (+
                    {formatKRW(item.optionPrice)} x {item.optionCount ?? 0})
                  </span>
                  <span className="infoboldPM">
                    {" "}
                    {formatKRW(
                      item.totalPrice ??
                        item.price * item.mainCount +
                          item.optionPrice * item.optionCount
                    )}
                    
                  </span>
                </div>
              </div>
            ))}

            <form className="buyformPM" onSubmit={handleSubmit}>
              <h3 className="h31PM"> </h3>
              <div className="buyinfoPM">
                <span></span>
                <input
                  type="text"
                  className="biidPM"
                  placeholder=" ."
                  value={buyer.name}
                  onChange={(e) => setBuyer({ ...buyer, name: e.target.value })}
                />
                <span></span>
                <input
                  type="tel"
                  className="biphonePM"
                  placeholder=" ."
                  value={buyer.phone}
                  onChange={(e) =>
                    setBuyer({ ...buyer, phone: e.target.value })
                  }
                />
              </div>

              <h3 className="h32PM"></h3>
              <div className="sendPM">
                <span></span>
                <input
                  type="text"
                  className="snPM"
                  placeholder=" ."
                  value={sender.name}
                  onChange={(e) => setSender({ name: e.target.value })}
                />
              </div>

              <div className="pitbPM">
                <h3 className="h33PM"> </h3>
                <div className="picbPM">
                  <input
                    type="checkbox"
                    id="directInput"
                    checked={true}
                    onChange={(e) => e.preventDefault()}
                  />
                  <label htmlFor="directInput">   </label>
                </div>
              </div>

              <div className="placeinfoPM">
                <span>  </span>
                <input
                  type="text"
                  className="piidPM"
                  placeholder="   ."
                  value={receiver.name}
                  onChange={(e) =>
                    setReceiver({ ...receiver, name: e.target.value })
                  }
                />
                <span></span>
                <input
                  type="tel"
                  className="piphonePM"
                  placeholder=" ."
                  value={receiver.phone}
                  onChange={(e) =>
                    setReceiver({ ...receiver, phone: e.target.value })
                  }
                />
                <span></span>
                <input
                  type="text"
                  className="piaddressPM"
                  placeholder=" ."
                  value={receiver.address}
                  onChange={(e) =>
                    setReceiver({ ...receiver, address: e.target.value })
                  }
                />
              </div>

              <h3 className="h35PM"></h3>
              <div className="couponPM">
                <span> </span>
                <div className="cpinfoPM">
                  <div className="currentcpboxPM">
                    <input
                      type="text"
                      className="cpcodePM"
                      placeholder=" ."
                      value={coupon}
                      readOnly //     
                    />
                    <button
                      type="button"
                      className="cpresetPM"
                      onClick={() => {
                        setCoupon(""); // input 
                        setSelectedCoupon(null); //   
                      }}
                    >
                      
                    </button>
                  </div>
                  <button
                    type="button"
                    className="slcpbtn"
                    onClick={() => setCouponModalVisible(true)}
                  >
                    
                  </button>
                </div>
                {couponModalVisible && (
                  <div
                    className="couponModal"
                    onClick={() => setCouponModalVisible(false)}
                  >
                    <div
                      className="couponModalContent"
                      onClick={(e) => e.stopPropagation()}
                    >
                      <h3>   </h3>
                      {userCoupons.length === 0 ? (
                        <p className="nocptext">  .</p>
                      ) : (
                        <ul>
                          {userCoupons.map((c, idx) => {
                            const isSelected = selectedCoupon?.text === c.text; //  
                            return (
                              <li
                                key={idx}
                                className="selectcpPM"
                                onClick={() => {
                                  setCoupon(c.text); // input  
                                  setSelectedCoupon(c); //     
                                  setCouponModalVisible(false); //  
                                }}
                              >
                                <input
                                  type="checkbox"
                                  readOnly
                                  checked={isSelected} //   
                                />
                                <span>
                                  {c.text} (
                                  {new Date(c.issuedAt).toLocaleDateString()}{" "}
                                  )
                                </span>
                              </li>
                            );
                          })}
                        </ul>
                      )}
                      <button
                        type="button"
                        className="modalclosePM"
                        onClick={() => setCouponModalVisible(false)}
                      >
                        
                      </button>
                    </div>
                  </div>
                )}
              </div>

              <h3 className="h36PM">  </h3>
              <div className="paywayPM">
                <div className="pwbtnboxPM">
                  <button
                    type="button"
                    className={`pwbtn1PM ${
                      payMethod === "card" ? "selectedPM" : ""
                    }`}
                    onClick={() => setPayMethod("card")}
                  >
                    
                  </button>
                  <button
                    type="button"
                    className={`pwbtn2PM ${
                      payMethod === "bank" ? "selectedPM" : ""
                    }`}
                    onClick={() => setPayMethod("bank")}
                  >
                     
                  </button>
                </div>

                {payMethod === "card" && (
                  <>
                    <span>  ( )</span>
                    <div className="pwcardPM">
                      <input
                        type="text"
                        maxLength="4"
                        value={cardInfo.num1}
                        onChange={(e) =>
                          setCardInfo({ ...cardInfo, num1: e.target.value })
                        }
                        required
                      />
                      <input
                        type="password"
                        maxLength="4"
                        value={cardInfo.num2}
                        onChange={(e) =>
                          setCardInfo({ ...cardInfo, num2: e.target.value })
                        }
                        required
                      />
                      <input
                        type="password"
                        maxLength="4"
                        value={cardInfo.num3}
                        onChange={(e) =>
                          setCardInfo({ ...cardInfo, num3: e.target.value })
                        }
                        required
                      />
                      <input
                        type="text"
                        maxLength="4"
                        value={cardInfo.num4}
                        onChange={(e) =>
                          setCardInfo({ ...cardInfo, num4: e.target.value })
                        }
                        required
                      />
                    </div>

                    <span> (/)</span>
                    <div className="pwymPM">
                      <input
                        type="number"
                        placeholder="YYYY"
                        value={cardInfo.year}
                        onChange={(e) =>
                          setCardInfo({ ...cardInfo, year: e.target.value })
                        }
                      />
                      <input
                        type="number"
                        placeholder="MM"
                        value={cardInfo.month}
                        onChange={(e) =>
                          setCardInfo({ ...cardInfo, month: e.target.value })
                        }
                        required
                      />
                    </div>

                    <span>  </span>
                    <input
                      type="password"
                      maxLength="2"
                      value={cardInfo.pw}
                      onChange={(e) =>
                        setCardInfo({ ...cardInfo, pw: e.target.value })
                      }
                      className="pwnum2PM"
                      required
                    />
                  </>
                )}
              </div>

              {payMethod === "bank" && (
                <div className="bankInfoPM">
                  <h4>  </h4>
                  <p>
                          .
                    <br />
                    <strong>  :</strong>  123-4567-8901-23
                    (: )
                    <br />
                    <strong>  :</strong>   24 
                  </p>
                  <p className="bankNoticePM">
                          .
                  </p>
                </div>
              )}

              <div className="pwagreetextPM">
                <span>
                        
                  .
                  <br />
                  <br />
                  
                  <br />
                  ,   ''  .
                  <br />
                          , 
                       .
                  <br />
                  <br />
                   
                  <br />
                     ' '   
                   .
                  <br />
                     .
                </span>
              </div>

              <div className="totalPricePM">
                <button type="submit" className="pwlastbtnPM">
                  {formatKRW(finalTotal)} 
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}
