import { useNavigate } from "react-router-dom"; //    
import "./css/SubButtons.css";

const buttons = [
  {
    img: "https://yuriyuri01.github.io/pina_img/pinaimg/sbtn1.png",
    label: "",
    path: "/Lottery",
    width: 55,
    height: 60,
  },
  {
    img: "https://yuriyuri01.github.io/pina_img/pinaimg/sbtn2.png",
    label: "/",
    width: 55,
    height: 60,
  },
  {
    img: "https://yuriyuri01.github.io/pina_img/pinaimg/sbtn3.png",
    label: "/",
    width: 55,
    height: 60,
  },
  {
    img: "https://yuriyuri01.github.io/pina_img/pinaimg/sbtn4.png",
    label: "/",
    width: 55,
    height: 53,
  },
  {
    img: "https://yuriyuri01.github.io/pina_img/pinaimg/sbtn5.png",
    label: "",
    width: 55,
    height: 60,
  },
  {
    img: "https://yuriyuri01.github.io/pina_img/pinaimg/sbtn6.png",
    label: "",
    width: 55,
    height: 60,
  },
  {
    img: "https://yuriyuri01.github.io/pina_img/pinaimg/sbtn7.png",
    label: "",
    width: 40,
    height: 55,
  },
  {
    img: "https://yuriyuri01.github.io/pina_img/pinaimg/sbtn8.png",
    label: "",
    width: 55,
    height: 55,
  },
];

const SubButtons = () => {
  const navigate = useNavigate(); //   

  return (
    <div id="subMP">
      <div id="sbtnMP">
        {buttons.map((btn, index) => (
          <div
            className="sbtnboxMP"
            key={index}
            onClick={() => {
              if (btn.path) {
                navigate(btn.path); //  path   
              }
            }}
            style={{ cursor: btn.path ? "pointer" : "pointer" }} //   
          >
            <div className="boxbgMP">
              <img
                src={btn.img}
                alt={btn.label}
                style={{ width: `${btn.width}px`, height: `${btn.height}px` }}
              />
            </div>
            <span>{btn.label}</span>
          </div>
        ))}
      </div>
    </div>
  );
};

export default SubButtons;
