import React, { useEffect, useState } from "react";
import "./css/Lottery.css";
import { Link } from "react-router-dom";

const cards = [
  {
    name: "  ",
    text: [" ", " "],
    back: "https://yuriyuri01.github.io/pina_img/pinaimg/lo_cardB1.png",
    couponText: " 15%  ",
  },
  {
    name: "",
    text: ["", ""],
    back: "https://yuriyuri01.github.io/pina_img/pinaimg/lo_cardB2.png",
    couponText: "15%  ",
  },
  {
    name: "",
    text: [" ", " "],
    back: "https://yuriyuri01.github.io/pina_img/pinaimg/lo_cardB3.png",
    couponText: "5%  ",
  },
  {
    name: " ",
    text: ["", ""],
    back: "https://yuriyuri01.github.io/pina_img/pinaimg/lo_cardB4.png",
    couponText: " 35%  ",
  },
  {
    name: "",
    text: ["", ""],
    back: "https://yuriyuri01.github.io/pina_img/pinaimg/lo_cardB5.png",
    couponText: " 8%  ",
  },
  {
    name: "",
    text: ["", ""],
    back: "https://yuriyuri01.github.io/pina_img/pinaimg/lo_cardB6.png",
    couponText: " 3%  ",
  },
  {
    name: "",
    text: ["", ""],
    back: "https://yuriyuri01.github.io/pina_img/pinaimg/lo_cardB7.png",
    couponText: "3,000  ",
  },
  {
    name: "",
    text: ["", ""],
    back: "https://yuriyuri01.github.io/pina_img/pinaimg/lo_cardB8.png",
    couponText: "3%  ",
  },
  {
    name: "",
    text: [" ", ""],
    back: "https://yuriyuri01.github.io/pina_img/pinaimg/lo_cardB9.png",
    couponText: "    (  300,000)",
  },
];

export default function Lottery() {
  const [stacked, setStacked] = useState(false);
  const [flippedIndex, setFlippedIndex] = useState(null);
  const [canPick, setCanPick] = useState(false);
  const [hasPicked, setHasPicked] = useState(false);
  const [selected, setSelected] = useState(null);
  const [modalVisible, setModalVisible] = useState(false);
  const [alreadyPickedToday, setAlreadyPickedToday] = useState(false);

  //   
  const savedUser = JSON.parse(localStorage.getItem("loggedInUser"));
  const userId = savedUser?.id;

  useEffect(() => {
    if (!userId) {
      //     
      setCanPick(false);
      return;
    }

    //      
    setCanPick(true);

    //  
    const today = new Date().toDateString();
    const key = `lottery_${userId}`;
    const lastPick = JSON.parse(localStorage.getItem(key));

    if (lastPick && lastPick.date === today) {
      //        
      setSelected(lastPick.card);
      setAlreadyPickedToday(true); //     
      setHasPicked(true);
      setCanPick(false);
      setTimeout(() => setModalVisible(true), 200);
      return;
    }
    //      
    const cardEls = document.querySelectorAll(".lo_card");
    const targetIndex = 4; // 0-based index  5 
    const targetRect = cardEls[targetIndex].getBoundingClientRect();

    cardEls.forEach((el) => {
      const rect = el.getBoundingClientRect();
      const dx =
        targetRect.left + targetRect.width / 2 - (rect.left + rect.width / 2);
      const dy =
        targetRect.top +
        targetRect.height / 2 -
        (rect.top + rect.height / 2) -
        150;
      el.style.setProperty("--dx", `${dx}px`);
      el.style.setProperty("--dy", `${dy}px`);
    });

    setStacked(true); // 5  

    const timer = setTimeout(() => {
      setStacked(false); //    
      setCanPick(true);
    }, 1200);

    return () => clearTimeout(timer);
  }, [userId]);

  const handleCardClick = (index) => {
    if (!userId) {
      //        
      alert("   ");
      return;
    }

    if (!canPick || hasPicked || flippedIndex !== null) return;
    setFlippedIndex(index);
    setHasPicked(true);

    setTimeout(() => {
      const chosenCard = cards[index];
      setSelected(chosenCard); //   

      //     ()
      if (userId && chosenCard.couponText) {
        const key = `coupons_${userId}`; //  
        const storedCoupons = JSON.parse(localStorage.getItem(key)) || [];
        storedCoupons.push({
          text: chosenCard.couponText,
          issuedAt: new Date().toISOString(), //   
        });
        localStorage.setItem(key, JSON.stringify(storedCoupons));

        //    
        const lotteryKey = `lottery_${userId}`;
        localStorage.setItem(
          lotteryKey,
          JSON.stringify({
            date: new Date().toDateString(),
            card: chosenCard,
          })
        );
      }
      setTimeout(() => setModalVisible(true), 50); // show   
    }, 1000);
  };

  const closeModal = () => {
    setModalVisible(false);
    setTimeout(() => setSelected(null), 500); //    
  };

  return (
    <div id="lottery">
      <div className="lo_title">
        <h1> </h1>
      </div>
      <div className="lo_cardBox">
        {cards.map((card, index) => (
          <div
            key={index}
            className={`lo_card ${stacked ? "centered" : ""} ${
              flippedIndex === index ? "is-flipped" : ""
            }`}
            onClick={() => handleCardClick(index)}
          >
            <div className="lo_cardInner">
              <div className="lo_cardFace lo_cardFront">
                <div className="lo_cardFrame">
                  <img
                    src="https://yuriyuri01.github.io/pina_img/pinaimg/card_logo.png"
                    alt=" "
                  />
                  <div className="lo_cardText">
                    {card.text.map((t) => (
                      <div key={t}>{t}</div>
                    ))}
                  </div>
                </div>
              </div>
              <div className="lo_cardFace lo_cardBack">
                <img src={card.back} alt={`${card.name}  `} />
              </div>
            </div>
          </div>
        ))}
      </div>

      {selected && (
        <div
          id="lo_modal"
          className={modalVisible ? "show" : ""}
          onClick={closeModal} //     
        >
          <div
            className="lo_modalWrap"
            onClick={(e) => e.stopPropagation()} //        
          >
            <div className="lo_modalHeader">
              <div className="name">{selected.name}</div>
              <div className="msg">{` ${selected.text.join(" ")} `}</div>
            </div>
            <img
              className="lo_modalImg"
              src={selected.back}
              alt={selected.name}
            />
            {alreadyPickedToday && (
              <p
                className="lo_notice"
                style={{ color: "#ffe9e9", marginTop: "10px" }}
              >
                  . <br />
                  .
              </p>
            )}
            <Link to="/mypage" className="lo_coupon" onClick={closeModal}>
              {selected.couponText ?? "10%  "}
            </Link>
            <div className="lo_modalFoot">
              <p className="lo_notice">
                *   .
              </p>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
