import { useState, useContext } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { CartContext } from "../CartContext"; // CartContext import
import "./css/MessageCard.css";

export default function MessageCard() {
  const navigate = useNavigate();
  const location = useLocation();
  const { items } = useContext(CartContext); // CartContext 

  const {
    cartItems,
    productName = " ",
    price = 59900,
    mainCount = 1,
    optionName = " ",
    optionPrice = 12000,
    optionCount = 1,
    deliveryType,
    img,
    detailVersion = "1",
  } = location.state || {};

  const products =
    cartItems && cartItems.length > 0
      ? cartItems.map((item) => ({
          id: item.id, //  key 
          optionLabel: item.optionLabel,
          deliveryType: item.deliveryType,
          productName: item.name,
          price: item.price,
          mainCount: item.qty,
          optionName: item.optionLabel,
          optionPrice: item.optionPrice,
          optionCount: item.optionQty || 0,
          img: item.img || "https://yuriyuri01.github.io/pina_img/pinaimg/dt_flower1.png",
        }))
      : [
          {
            id: 0, //   id
            optionLabel: optionName,
            deliveryType,
            productName,
            price,
            mainCount,
            optionName,
            optionPrice,
            optionCount,
            img: img || "https://yuriyuri01.github.io/pina_img/pinaimg/dt_flower1.png",
          },
        ];

  const grandTotal = products.reduce(
    (sum, item) =>
      sum + item.price * item.mainCount + item.optionPrice * item.optionCount,
    0
  );

  const categories = [
    { name: "DIY", img: "https://yuriyuri01.github.io/pina_img/pinaimg/mc_card.png" },
    {
      name: " ",
      img: "https://yuriyuri01.github.io/pina_img/pinaimg/mc_card2.png",
    },
    {
      name: "  .",
      img: "https://yuriyuri01.github.io/pina_img/pinaimg/mc_card3.png",
    },
    {
      name: "Happy BirthDay !",
      img: "https://yuriyuri01.github.io/pina_img/pinaimg/mc_card4.png",
    },
    {
      name: "   !",
      img: "https://yuriyuri01.github.io/pina_img/pinaimg/mc_card5.png",
    },
  ];

  const [category, setCategory] = useState(categories[0].name);
  const [message, setMessage] = useState("");

  const handleCategoryChange = (e) => setCategory(e.target.value);
  const handleMessageChange = (e) => {
    if (e.target.value.length <= 250) setMessage(e.target.value);
  };

  const handleSubmit = () => {
    //  cartItems   
    navigate("/payment", {
      state: {
        cartItems: products,
        productName: products[0].productName,
        price: products[0].price,
        mainCount: products[0].mainCount,
        optionName: products[0].optionName,
        optionPrice: products[0].optionPrice,
        optionCount: products[0].optionCount,
        totalPrice: grandTotal,
        deliveryType: products.map((item) => item.deliveryType),
        removeKeys: products.map(
          (item) => `${item.id}-${item.deliveryType}-${item.optionLabel}`
        ), //  key
      },
    });
    window.scrollTo(0, 0);
  };

  const selectedImage = categories.find((cat) => cat.name === category)?.img;
  const detailImg =
    detailVersion === "1"
      ? "https://yuriyuri01.github.io/pina_img/pinaimg/dt_detail1.png"
      : "https://yuriyuri01.github.io/pina_img/pinaimg/dt_detail1-2.png";

  return (
    <div>
      {/* 1 */}
      <div id="sec1allMC">
        <h1 className="sec1titleMC">  </h1>

        <div className="product-listMC">
          {products.map((item, idx) => {
            const totalPrice =
              item.price * item.mainCount + item.optionPrice * item.optionCount;
            return (
              <div key={idx} className="product-itemMC">
                <div className="sec1leftMC">
                  <img src={item.img} alt={item.productName} />
                </div>
                <div className="sec1rightMC">
                  <div className="delivery-typeMC">
                    {item.deliveryType === "" ? "" : ""}
                  </div>
                  <div className="itemnameMC">{item.productName}</div>
                  <div className="item-detailMC">
                    <div>
                      {item.price.toLocaleString()} x {item.mainCount}
                      {item.optionCount > 0 && (
                        <div className="optionMC">
                          {item.optionName}(+{item.optionPrice.toLocaleString()}
                           x {item.optionCount})
                        </div>
                      )}
                    </div>
                    <div className="itempriceMC">
                       {totalPrice.toLocaleString()}
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </div>

        <div className="grand-totalMC">
            : {grandTotal.toLocaleString()}
        </div>
      </div>

      {/* 2 */}
      <div id="sec2allMC">
        <div className="sec2topMC">
          {categories.map((cat, idx) => {
            const checkboxId = `category-${idx}`;
            return (
              <label
                key={cat.name}
                className="circle-checkboxMC"
                htmlFor={checkboxId}
              >
                <input
                  type="checkbox"
                  name="category"
                  value={cat.name}
                  checked={category === cat.name}
                  onChange={handleCategoryChange}
                  id={checkboxId}
                />
                {cat.name}
              </label>
            );
          })}
        </div>
        <div className="sec2bottomMC">
          <div className="sec2bottomleftMC">
            <img src={selectedImage} alt={category} />
          </div>
          <div className="sec2bottomrightMC">
            <div className="input-containerMC">
              <textarea
                id="messageMC"
                value={message}
                onChange={handleMessageChange}
                maxLength={250}
                placeholder=" ."
              />
              <div className="char-countMC">
                <span id="charCountMC">{message.length}</span>/250
              </div>
            </div>
          </div>
        </div>
        <div id="sec2-2allboxMC">
          <br />-  10,    26 . ( )
          <br />-     .
          <br />-       .
        </div>
      </div>

      {/* 3 */}
      <div id="sec3MC">
        <button className="doneMC" onClick={handleSubmit}>
          
        </button>
      </div>
    </div>
  );
}
