import { useState, useEffect } from "react";
import "./css/WeatherBanner.css";

const API_KEY = "1ac517df27cdf8836585f85daf01a89b";

const WeatherBanner = () => {
  const [weather, setWeather] = useState(null);

  useEffect(() => {
    const fetchWeather = async () => {
      try {
        const response = await fetch(
          `https://api.openweathermap.org/data/2.5/weather?lat=37.3084&lon=126.851&appid=${API_KEY}&units=metric&lang=kr`
        );
        const data = await response.json();
        setWeather(data);
      } catch (error) {
        console.error("  :", error);
      }
    };
    fetchWeather();
  }, []);

  //   
  const getWeatherImage = () => {
    if (!weather)
      return `https://yuriyuri01.github.io/pina_img/pinaimg/default-weather.png`;
    const main = weather.weather[0].main.toLowerCase();
    if (main.includes("clear"))
      return `https://yuriyuri01.github.io/pina_img/pinaimg/weather1.png`; // 
    if (main.includes("cloud"))
      return `https://yuriyuri01.github.io/pina_img/pinaimg/weather2.png`; // 
    if (main.includes("rain"))
      return `https://yuriyuri01.github.io/pina_img/pinaimg/weather3.png`; // 
    return `https://yuriyuri01.github.io/pina_img/pinaimg/default-weather.png`;
  };

  //   
  const getSimpleWeatherText = () => {
    if (!weather) return "";
    const main = weather.weather[0].main.toLowerCase();
    if (main.includes("clear")) return "";
    if (main.includes("cloud")) return "";
    if (main.includes("rain")) return "";
    return "  ";
  };

  return (
    <div
      className="custom-banner active"
      style={{
        backgroundImage: `url(${getWeatherImage()})`,
        backgroundPosition: "center",
      }}
    >
      {weather ? (
        <div className="weather-info">
          <h2>  : {getSimpleWeatherText()}</h2>
          <div className="wipbox">
            <p className="wip1">
               : {weather.main.temp}C /  {weather.main.feels_like}C
            </p>
            <p className="wip2">
               /  : {weather.main.temp_min}C / {weather.main.temp_max}
              C
            </p>
            <p className="wip3"> : {weather.main.humidity}%</p>
            <p className="wip4"> : {weather.wind.speed} m/s</p>
            <p className="wip5"> : {weather.clouds.all}%</p>
          </div>
        </div>
      ) : (
        <p>   ...</p>
      )}
    </div>
  );
};

export default WeatherBanner;
