import React, { useState, useEffect } from "react";
import "./css/BoardEdit.css";
import { useParams, useNavigate } from "react-router-dom";

const BoardEdit = () => {
  const { id } = useParams();
  const navigate = useNavigate();

  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [file, setFile] = useState(null);
  const [userId, setUserId] = useState("");
  const [authorId, setAuthorId] = useState("");

  //    
  const [existingFile, setExistingFile] = useState(null);
  const [existingFileName, setExistingFileName] = useState("");

  useEffect(() => {
    const savedUser = JSON.parse(localStorage.getItem("loggedInUser"));
    if (savedUser && savedUser.id) setUserId(savedUser.id);

    const savedPosts = JSON.parse(localStorage.getItem("posts")) || [];
    const found = savedPosts.find((p, i) => i === Number(id));
    if (found) {
      setTitle(found.title);
      setContent(found.content);
      setAuthorId(found.name);

      if (found.productImg) {
        setFile({ name: found.fileName, data: found.productImg });
        setExistingFile(found.productImg);
        setExistingFileName(found.fileName);
      }
    }
  }, [id]);

  const handleFileChange = (e) => {
    if (e.target.files.length > 0) {
      const selectedFile = e.target.files[0];
      const reader = new FileReader();
      reader.onloadend = () => {
        setFile({ name: selectedFile.name, data: reader.result });
      };
      reader.readAsDataURL(selectedFile);
    } else {
      setFile(null);
    }
  };

  const handleUpdate = (e) => {
    e.preventDefault();

    if (!title) {
      alert(" .");
      return;
    }
    if (!content) {
      alert(" .");
      return;
    }
    if (userId !== authorId) {
      alert("    .");
      return;
    }

    const savedPosts = JSON.parse(localStorage.getItem("posts")) || [];

    const updatePost = (imageData) => {
      const shortId = userId.includes("@") ? userId.split("@")[0] : userId;
      const updated = savedPosts.map((p, i) =>
        i === Number(id)
          ? {
            ...p,
            title,
            content,
            productImg: imageData || existingFile,
            fileName: file?.name || existingFileName,
            name: shortId,
          }
          : p
      );

      localStorage.setItem("posts", JSON.stringify(updated));
      alert(" !");
      navigate("/Community");
    };

    if (file && file.data) {
      updatePost(file.data);
    } else {
      updatePost(existingFile);
    }
  };

  const handleDelete = () => {
    if (userId !== authorId) {
      alert("    .");
      return;
    }
    if (!window.confirm(" ?")) return;

    const savedPosts = JSON.parse(localStorage.getItem("posts")) || [];
    const filtered = savedPosts.filter((_, i) => i !== Number(id));

    localStorage.setItem("posts", JSON.stringify(filtered));
    alert(" !");
    navigate("/Community");
  };

  return (
    <div id="EsecallBW">
      <div className="Esec1titleQA"> </div>

      <div className="Esec2QA">
        <div className="Esec2innerQA">
          <div className="Esec2leftQA"> </div>
          <div className="Esec2rightQA">
            <div className="Esec2rightinnerQA">
              <div className="Esec2inputQA">
                <label htmlFor="userId"></label>
                <input
                  type="text"
                  id="userId"
                  name="userId"
                  value={userId}
                  readOnly
                />
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="Esec3QA">
        <div className="Esec3innerQA">
          <div className="Esec3leftQA"> </div>
          <div className="Esec3rightQA">
            <div className="Esec3inputQA">
              <label htmlFor="title"></label>
              <input
                type="text"
                id="title"
                placeholder=" ."
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                disabled={userId !== authorId}
              />
            </div>

            <div className="Esec3inputQA">
              <label htmlFor="content"> </label>
              <textarea
                id="Esec3contentQA"
                placeholder=" .(1000 )"
                value={content}
                onChange={(e) => setContent(e.target.value)}
                disabled={userId !== authorId}
              />
            </div>

            <div className="Esec3inputQA EfileGroup">
              <label htmlFor="file"></label>
              <div className="Efile-wrapperQA">
                <span className="Efile-labelQA">
                  {file?.name || "  "}
                </span>

                <button
                  type="button"
                  onClick={() => document.getElementById("file").click()}
                  id="EcustomBtn"
                  disabled={userId !== authorId}
                >
                  
                </button>
                <input
                  type="file"
                  id="file"
                  hidden
                  onChange={handleFileChange}
                  disabled={userId !== authorId}
                />
              </div>
              <span className="EfileNote">* 500MB</span>
            </div>
          </div>
        </div>
      </div>

      {userId === authorId ? (
        <div id="Esec5QA">
          <button className="Esec5buttomQA" onClick={handleUpdate}>
            
          </button>
          <button className="Esec5buttomQA" onClick={handleDelete}>
            
          </button>
        </div>
      ) : (
        <div id="Esec5QA">
          <button
            className="Esec5buttomQA"
            onClick={() => navigate("/Community")}
          >
            
          </button>
        </div>
      )}

    </div>
  );
};

export default BoardEdit;
