import { useEffect } from "react";
import { loadKakaoSDK } from "../utils/loadKakaoSDK";
import "./css/SocialLog.css";

const GOOGLE_CLIENT_ID =
  "1052122433520-qog214nebl65vvq27c8gbo26b5v6klkp.apps.googleusercontent.com";
const KAKAO_JS_KEY = "245cbbe08cb5a39eddee06fbb1ffb99d";

export default function SocialLog({ setLoggedInUser }) {
  useEffect(() => {
    const script = document.createElement("script");
    script.src = "https://accounts.google.com/gsi/client";
    script.async = true;
    script.onload = () => {
      window.google.accounts.id.initialize({
        client_id: GOOGLE_CLIENT_ID,
        callback: handleGoogleResponse,
      });
      window.google.accounts.id.renderButton(
        document.getElementById("googleSignInDiv"),
        { theme: "filled_blue", size: "medium" }
      );
    };
    document.body.appendChild(script);

    loadKakaoSDK(KAKAO_JS_KEY).then(() => console.log("Kakao SDK  "));
  }, []);

  const handleGoogleResponse = (response) => {
    const base64Url = response.credential.split(".")[1];
    const base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
    const jsonPayload = decodeURIComponent(
      atob(base64)
        .split("")
        .map((c) => "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2))
        .join("")
    );
    const user = JSON.parse(jsonPayload);

    //    
    const shortId = user.email.includes("@")
      ? user.email.split("@")[0]
      : user.email;

    const googleUser = {
      id: shortId, //   
      name: user.name,
      email: user.email,
    };

    setLoggedInUser(googleUser);
    localStorage.setItem("loggedInUser", JSON.stringify(googleUser));
    window.dispatchEvent(new Event("userChange"));
  };

  const handleKakaoLogin = () => {
    loadKakaoSDK(KAKAO_JS_KEY).then((Kakao) => {
      window.Kakao.Auth.login({
        success: () => {
          Kakao.API.request({
            url: "/v2/user/me",
            success: (res) => {
              const kakaoUser = {
                id: res.kakao_account?.profile?.nickname,
                name: res.kakao_account?.profile?.nickname || "Kakao ",
                email: res.kakao_account?.email || "",
              };
              setLoggedInUser(kakaoUser);
              localStorage.setItem("loggedInUser", JSON.stringify(kakaoUser));
              window.dispatchEvent(new Event("userChange")); //  NavChat  
            },
          });
        },
      });
    });
  };

  const handleNaverLogin = () => {
    const naverUser = {
      id: "naver@example.com",
      name: "",
      email: "naver@example.com",
    };
    setLoggedInUser(naverUser);
    localStorage.setItem("loggedInUser", JSON.stringify(naverUser));
    window.dispatchEvent(new Event("userChange")); //  NavChat  
  };

  return (
    <div className="lgsnsbox">
      <div className="lgnaverbox" onClick={handleNaverLogin}>
        <img
          src="https://yuriyuri01.github.io/pina_img/pinaimg/naverlogo.png"
          alt=" "
        />
        <span className="naverspan"></span>
      </div>
      <div
        className="lggooglebox"
        style={{
          position: "relative",
          width: "75px",
        }}
      >
        <div
          id="googleSignInDiv"
          style={{
            position: "absolute",
            top: "21px",
            left: 0,
            width: "75px",
            height: "75px",
            opacity: 0,
            cursor: "pointer",
          }}
        ></div>
        <div
          className="custom-google-img"
          style={{
            width: "75px",
            height: "75px",
            borderRadius: "50%",
            backgroundImage: `url(https://yuriyuri01.github.io/pina_img/pinaimg/google-circle.png)`,
            backgroundSize: "cover",
            margin: "0 auto",
            pointerEvents: "none",
          }}
        ></div>
        <span className="googlespan"></span>
      </div>
      <div className="lgkakaobox" onClick={handleKakaoLogin}>
        <img
          src="https://yuriyuri01.github.io/pina_img/pinaimg/kakaologo.png"
          alt=" "
        />
        <span className="kakaospan"></span>
      </div>
    </div>
  );
}
