import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "./css/QnA.css";

const QnA = () => {
  const [password, setPassword] = useState("");
  const [inquiryType, setInquiryType] = useState("");
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [file, setFile] = useState(null);
  const [agree, setAgree] = useState(false);
  const [userId, setUserId] = useState("");
  const navigate = useNavigate();

  //   
  useEffect(() => {
    const savedUser = JSON.parse(localStorage.getItem("loggedInUser"));
    if (savedUser && savedUser.id) {
      setUserId(savedUser.id); //   
    }
  }, []);

  const handleFileChange = (e) => {
    if (e.target.files.length > 0) {
      setFile(e.target.files[0]);
    } else {
      setFile(null);
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!password) {
      alert(" .");
      return;
    }

    if (!inquiryType) {
      alert("  .");
      return;
    }
    if (!title) {
      alert(" .");
      return;
    }
    if (!content) {
      alert(" .");
      return;
    }
    if (!agree) {
      alert("   .");
      return;
    }

    if (!agree) {
      alert("   .");
      return;
    }

    alert(" !");
    navigate("/mypage");
  };

  return (
    <div id="secallQA">
      <div className="sec1titleQA">QnA</div>

      {/*   */}
      <div className="sec2QA">
        <div className="sec2innerQA">
          <div className="sec2leftQA"> </div>
          <div className="sec2rightQA">
            <div className="sec2rightinnerQA">
              <div className="sec2inputQA">
                <label htmlFor="userId"></label>
                <input
                  type="text"
                  id="userId"
                  name="userId"
                  value={userId}
                  readOnly
                />
              </div>
              <div className="sec2inputQA">
                <label htmlFor="password"></label>
                <input
                  type="password"
                  id="password"
                  placeholder="   ."
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
              </div>
            </div>
          </div>
        </div>
      </div>

      {/*   */}
      <div className="sec3QA">
        <div className="sec3innerQA">
          <div className="sec3leftQA"> </div>
          <div className="sec3rightQA">
            <div className="sec3inputQA">
              <label className="inquirytypeQA" htmlFor="inquiryType">
                 
              </label>
              <select
                id="inquiryType"
                value={inquiryType}
                onChange={(e) => setInquiryType(e.target.value)}
              >
                <option value="">  .</option>
                <option value=""></option>
                <option value="/">/</option>
                <option value="/">/</option>
                <option value=""></option>
              </select>
            </div>

            <div className="sec3inputQA">
              <label htmlFor="title"></label>
              <input
                type="text"
                id="title"
                placeholder=" ."
                value={title}
                onChange={(e) => setTitle(e.target.value)}
              />
            </div>

            <div className="sec3inputQA">
              <label htmlFor="content"> </label>
              <textarea
                id="sec3contentQA"
                placeholder=" .(1000 )"
                value={content}
                onChange={(e) => setContent(e.target.value)}
              />
            </div>

            <div className="sec3inputQA fileGroup">
              <label htmlFor="file"></label>
              <div className="file-wrapperQA">
                <span className="file-labelQA">
                  {file ? file.name : "  "}
                </span>
                <button
                  type="button"
                  onClick={() => document.getElementById("file").click()}
                  id="customBtn"
                >
                  
                </button>
                <input
                  type="file"
                  id="file"
                  hidden
                  onChange={handleFileChange}
                />
              </div>
              <span className="fileNote">* 500MB</span>
            </div>
          </div>
        </div>
      </div>

      {/*  4 */}
      <div id="sec4QA">
        <div className="sec4innerQA">
          <div className="sec4leftQA">  </div>
          <div className="sec4rightQA">
            <p>
                
              <br />
               ( )   30 
                     
                       ,
              .
              <br />
              <br />
              1(    )
              <br />
                   .  
                    , 
                   18   
                   .
              <br />
              <br />
              2(    )
              <br />
                   .  
                 ,   
              . ,       
                .
            </p>

            <div className="agreeCheckQA">
              <input
                type="checkbox"
                id="agree"
                checked={agree}
                onChange={(e) => setAgree(e.target.checked)}
              />
              <label htmlFor="agree">
                    ()
              </label>
            </div>
          </div>
        </div>
      </div>

      {/*    */}
      <div id="sec5QA">
        <button type="submit" className="sec5buttomQA" onClick={handleSubmit}>
           
        </button>
      </div>
    </div>
  );
};

export default QnA;
